import React, { useState, useEffect } from "react";

function Btn({ children, onClick, className }) {
	return (
		<button className={className} onClick={onClick}>
			{children}
		</button>
	);
}

function Counter({ sendDataToParent }) {
	const [counter, updateCounter] = useState(0);

	function handleIncrement() {
		updateCounter(counter + 1);
		sendDataToParent(counter + 1);
	}

	function handleDecrement() {
		updateCounter(counter <= 0 ? 0 : counter - 1);
		sendDataToParent(counter <= 0 ? 0 : counter - 1);
	}

	function handleReset() {
		updateCounter(0);
	}

	useEffect(() => {}, [counter]);

	return (
		<div className="container mx-auto p-4 flex text-center">
			<Btn
				className="bg-green-400 hover:bg-green-300 text-gray-800 text-3xl font-semibold p-6 border rounded shadow"
				onClick={handleIncrement}
			>
				+
			</Btn>
			<span className="text-6xl py-3 mx-3 wi">{counter}</span>
			<Btn
				className="bg-red-400 hover:bg-red-300 text-gray-800 text-3xl font-semibold p-6 border rounded shadow"
				onClick={handleDecrement}
			>
				-
			</Btn>

			<div className="flex col-auto">
				<Btn
					className="bg-yellow-400 hover:bg-yellow-300 text-gray-800 font-semibold py-2 px-4 border rounded shadow"
					onClick={handleReset}
				>
					Reset
				</Btn>
			</div>
			<br />
		</div>
	);
}

export default Counter;
