import { useState } from "react";
import Counter from "./components/Counter";
import InputText from "./components/InputText";

export default function App() {
	const [costUnit, setCostUnit] = useState(0);
	const [costTip, setCostTip] = useState(0);
	const [itemDrink, setItemDrink] = useState();

	function handleCostUnit(currentValue) {
		setCostUnit(currentValue);
	}

	function handleCostTip(currentValue) {
		setCostTip(currentValue);
	}

	const sendDataToParent = (index) => {
		setItemDrink(index);
	};

	return (
		<div>
			<header>
				<div className="bg-gray-100 mx-auto p-4">
					<h1 className="text-center font-semibold text-xl">
						Contador de Choop
					</h1>
				</div>
			</header>

			<main>
				<div className="container mx-auto p-4 flex text-center justify-center">
					Custo parcial (sem a gorgeta): R$&nbsp;
					{(costUnit * itemDrink).toFixed(2)}
					<br />
					Valor da gorgeta: R$&nbsp;
					{((costUnit * itemDrink) / (costTip * 1)).toFixed(2)}
					<br />
					Custo total (com a gorgeta): R$&nbsp;
					{(
						costUnit * itemDrink +
						(costUnit * itemDrink) / (costTip * 100)
					).toFixed(2)}
				</div>
				<div className="mx-auto p-4">
					<Counter sendDataToParent={sendDataToParent}></Counter>
					<InputText
						labelText="Valor do chopp (R$)"
						inputValue={costUnit}
						onInputChange={handleCostUnit}
						step=".01"
					></InputText>
					<InputText
						labelText="Gorgeta (%)"
						placeholderText=""
						inputValue={costTip}
						onInputChange={handleCostTip}
						step="1"
					></InputText>
				</div>
			</main>
		</div>
	);
}
